@import url(https://fonts.googleapis.com/css2?family=Frijole&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}::-webkit-scrollbar{display:none}

body{background:#4cc6da;background:linear-gradient(90deg, #4cc6da 0%, #95d9f0 50%, #876cd6 100%)}.app{display:flex;flex-direction:column;align-items:center}

.contentWrapper{padding:2rem 0;width:95vw;margin:3rem auto;display:grid;grid-template-columns:repeat(auto-fit, 40rem);grid-gap:3rem 3rem;justify-content:space-around}.monsterCard{border-radius:40px;background-color:#0ba6c9;height:500px;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;font-size:2rem;cursor:pointer;transition:all 0.2s ease-in;border:3px solid #fff}.monsterCard:hover{border:1px solid #eee;box-shadow:10px 10px 40px #727272}

